class UserController {
  static parseJSON() {
    let parse;

    try {
      parse = JSON.parse(window.document.cookie);
    } catch(ex) {
      parse = {
        email: '',
        cart: []
      };
    }

    return parse;
  }

  static getEmail() {
    let parse = UserController.parseJSON();
    return parse.email;
  }

  static signedIn() {
    return !!UserController.getEmail();
  }

  static signIn(email, pass) {
    window.document.cookie = JSON.stringify({
      email: email,
      cart: []
    });
  }

  static signOut() {
    window.document.cookie = '';
  }

  static appendCartItem(catId, prodId, qty) {
    let parse = UserController.parseJSON();

    parse.cart.push({
      catId: catId,
      prodId: prodId,
      qty: qty
    });

    window.document.cookie = JSON.stringify(parse);
  }
}

export default UserController;
